<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      rect: null,
      time: "2019-4-20",
      title: "事件",
      items: [
        {
          title: "事件监听器addEventListener",
          target: "#eventlistener"
        },
        { divider: true },
        {
          title: "移除事件监听器",
          target: "#removelistener"
        },
        { divider: true },
        {
          title: "事件捕获",
          target: "#capture"
        },
        { divider: true },
        {
          title: "事件冒泡",
          target: "#bubble"
        },
        { divider: true },
        {
          title: "自定义事件",
          target: "#selfevent"
        },
        { divider: true },
      ],
      pres: 
[
`   
    const ul = document.querySelector("ul");
    const middleBox = document.querySelector('#middle');
    const topBox = document.querySelector('#top');
    const bottomBox = document.querySelector('#bottom');
    topBox.addEventListener('click', e => {
        const li = document.createElement('li');
        li.textContent = 'top';
        ul.appendChild(li);
    }, true)
    bottomBox.addEventListener('click', e => {
        const li = document.createElement('li');
        li.textContent = 'bottom';
        ul.appendChild(li);
    }, true)
    middleBox.addEventListener('click',  e => {
        const li = document.createElement('li');
        li.textContent = 'middle';
        ul.appendChild(li);
    }, true) 
`,
`
    <div id="top">
        <div id="middle">
            <div id="bottom"></div>
        </div>
    </div> 
    <div id="result">
        <p>运行结果如下：</p>
        <ul></ul>
    </div>
`,
`
    #top{
        width: 200px;
        height: 200px;
        background-color: antiquewhite;
        display: flex;
        justify-content: center;
        align-items: center;
        float: left;
    }
    #middle{
        width: 150px;
        height: 150px;
        background-color: aquamarine;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    #bottom{
        width: 100px;
        height: 100px;
        background-color: azure;
    }
    
    #result{
        float: left;
        width: 200px;
        height: 200px;
        background-color: aliceblue;
        margin-left: 15px;
        font-size: 12px;
        color: darkgray;
        padding: 20px;
        box-sizing: border-box;
        overflow: auto;
    }
    #result ul{
        list-style: none;
    }
`,
`
    const ul = document.querySelector("ul");
    const middleBox = document.querySelector('#middle');
    const topBox = document.querySelector('#top');
    const bottomBox = document.querySelector('#bottom');
    topBox.addEventListener('click', e => {
        const li = document.createElement('li');
        li.textContent = 'top';
        ul.appendChild(li);
    }, false)
    bottomBox.addEventListener('click', e => {
        const li = document.createElement('li');
        li.textContent = 'bottom';
        ul.appendChild(li);
    }, false)
    middleBox.addEventListener('click',  e => {
        const li = document.createElement('li');
        li.textContent = 'middle';
        ul.appendChild(li);
    }, false) 
`,
`
    box.addEventListener(
        'click',                                // 事件名
        e => { console.log(e); },               // 事件监听函数
        {
            capture: false,                     // 是否捕获
            once: true,                         // 执行一次后删除
            passive: true                       // 设置为 true 暗示浏览器我不会调用preventDefault，从而让浏览器更加顺滑的处理wheel这类事件
        }
    )
`,
`
    const fn = e => {
        console.log(e);
    };
    box.addEventListener(
        'click', 
        fn,
        true
    )
    box.removeEventListener(
        'click',
        fn,         // 函数必须一致
        true        // 指定的捕获形式，必须和addEventListener定义的一致才能删除成功
    )
`,
`
    const box = document.querySelector('#box');
    const ev = new Event('fn', {
        bubbles: true,          // 是否能冒泡
        cancelable: true,       // 是否能取消
        composed: false,        // 是否能够在影子元素外触发
    })

    box.addEventListener('fn', (e) => {

        // 是否能够冒泡
        if(e.bubbles) {
            // 下面两种方式都能取消冒泡
            // e.cancelBubble = true;
            e.stopPropagation();
        }

        // 事件是否能够取消
        if(e.cancelable) { 
            // 如果事件不能取消，调用preventDefault会报错
            e.preventDefault(); 
        }

        // 调用下面的方法，会使只有当前被addEventListener监听的函数能够触发，其余不能
        e.stopImmediatePropagation(); 

        /**
         * 其他常用的属性
         * 1. e.defaultPrevented    是否触发过preventDefault
         * 2. e.currentTarget       绑定事件的元素
         * 3. e.target              触发事件的元素，在ie中叫srcElement
         * 4. e.type                获取事件名
        */
    })
    
    // 触发方法
    box.dispatchEvent(ev); 
`,
`
    const box = document.querySelector('#box');

    // 创建自定义事件
    const ce = new CustomEvent("fn", { 
        detail: { // 传给事件触发函数的数据
            a: "aa"
        }
    })
    box.addEventListener("fn", function(e){
        console.log(e.detail);
    });
    box.dispatchEvent(ce);
`,
`
    const et = new EventTarget(); // 事件绑定对象

    const ce = new CustomEvent("fn", { 
        detail: {
            a: "aa"
        }
    })
    et.addEventListener("fn", function(e){
        console.log(e.detail);
    });
    et.dispatchEvent(ce);

`
]
    };
