        const topBox = document.querySelector('#top');
        const bottomBox = document.querySelector('#bottom');
        // 事件捕获 从dom树起点向目标元素调用
        // 事件冒泡 从目标元素向dom树起点调用
        topBox.addEventListener('click', e => {
            const li = document.createElement('li');
            li.textContent = 'top';
            ul.appendChild(li);
        }, true)
        bottomBox.addEventListener('click', e => {
            const li = document.createElement('li');
            li.textContent = 'bottom';
            ul.appendChild(li);
        }, true)
        middleBox.addEventListener('click',  e => {
            const li = document.createElement('li');
            li.textContent = 'middle';
            ul.appendChild(li);
        }, true) // 是否使用捕获

        const ul1 = document.querySelector(".ul1");
        const middleBox1 = document.querySelector('#middle1');
        const topBox1 = document.querySelector('#top1');
        const bottomBox1 = document.querySelector('#bottom1');
        // 事件捕获 从dom树起点向目标元素调用
        // 事件冒泡 从目标元素向dom树起点调用
