            const li = document.createElement('li');
            li.textContent = 'top';
            ul1.appendChild(li);
        }, false)
        bottomBox1.addEventListener('click', e => {
            const li = document.createElement('li');
            li.textContent = 'bottom';
            ul1.appendChild(li);
        }, false)
        middleBox1.addEventListener('click',  e => {
            const li = document.createElement('li');
            li.textContent = 'middle';
            ul1.appendChild(li);
        }, false) // 是否使用捕获
    },
    methods: {
    },
    destroyed() {

    },
};
</script>

